.nav {
  background: transparent;
  padding: 0;
  margin: 0 0 0 8px;
  width: auto;

  li {
    list-style: none;
    display: inline;

    &.active > a:before {
      border-bottom: 12px solid #FFF;
      border-left: 12px solid transparent;
      border-right: 12px solid transparent;
      bottom: 0;
      content: '';
      height: 0;
      left: 50%;
      margin-left: -12px;
      position: absolute;
      width: 0;
    }

    &.active > a:hover {
      background: none;
      box-shadow: none;
      color: inherit;
      cursor: default;
    }
  }
}

.nav li a {
  color: #FFF;
  font-weight: 400;
  font-size: 19px;
  line-height: 24px;
  margin: 0;
  padding: 16px;
  text-decoration: none;
  display: inline-block;
  position: relative;

  &:hover, &:focus {
    background-color: #FFF;
    box-shadow: 0px 2px 8px 0px rgba(84,87,89,0.32);
    color: #007c89;
  }

  &:focus {
    outline: 0;
  }
}

.dropdown-link {
  &:hover .dropdown__arrow, &:focus .dropdown__arrow {
    fill: #007c89;
  }
  .dropdown__arrow {
    position: relative;
    top: -3px;
    margin-left: 8px;
    fill: #fff;
  }
}
