.container {
  width: 980px;
  margin: 0 auto;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

h1 {
  font-size: 40px;
  font-weight: 300;
  margin-left: 24px;
  color: #515761;
}
h2 {
  font-size: 34px;
  font-weight: 300;
  color: rgba(0, 0, 0, 1);
  margin-bottom: 16px;
  padding-bottom: 0;
}
a.h3 {
  font-size: 24px;
  color: #007c89;
  text-decoration: underline;
  margin: 0;
  padding: 0;
  font-weight: 400;
}
h4 {
  font-size: 16px;
  font-weight: 500;
  color: #515761;
  text-transform: uppercase;
  margin-left: 24px;
}
h5 {
  font-size: 20px;
  font-weight: 300;
  margin: 0;
  padding: 0;
}
p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  color: #363f47;
  padding: 0;
  margin: 0;
}

.card {
  border-top: 1px solid #979aa0;
  border-bottom: 1px solid #979aa0;
  display: flex;
  flex-direction: row;
  margin: -1px 0 0 0;
  padding: 24px;

  &.light-green {
    background: #F3FAFA;
  }
  &.light-blue {
    background: #F2FAFD;
  }
  &.light-gray {
    background: #f8f8f9;
  }

  &.no-top-border {
    border-top: 0;
  }

  h3 {
    font-size: 24px;
    font-weight: 300;
    margin: 0 0 8px;
    padding: 0;
  }
  h3.heading {
    font-weight: 500;
  }
}
.col-fixed-82 {
  width: 82px;
}
.col-1 {
  flex: 1;
}
.col-2 {
  display: flex;
  flex-direction: column;
  flex: 5;
}
.card-top {
  margin-bottom: 8px;
}
.card-body {
  display: flex;
  flex-direction: row;
  flex: 3;
  display: flex;
  flex-direction: row;
}



.help-box {
  background-color: #ffffff;
  border: 1px solid #14568d;
  border-radius: 3px;
  padding: 16px;
  margin: 0 8px;
  text-align: center;
  position: relative;
  height: 180px;



  p {
    color: #515761;
    font-weight: 400;
    position: absolute;
    width: 100%;
    left: 0;
    top: 120px;
    text-align: center;
    z-index: 1;
    transition: top .5s ease;
  }

  button {
    position: absolute;
    width: 180px;
    left: 50%;
    margin-left: -90px;
    top: 130px;
    opacity: 0;
    transition: opacity .35s ease;
  }

  &:hover, &:focus {
    background-color: #14568d;
    box-shadow: 0px 0px 4px 2px rgba(0,0,0,0.2);
    outline: 0;

    p {
      color: #fff;
      top: 100px;
    }

    button {
      opacity: 1;
    }

    svg, use, stroke, path {
      fill: #fff;
    }
  }
}
