.overpanel .header .close:focus, .overpanel .header .close.focus {
  box-shadow: 0px 0px 0px 2px #009BDF, 0px 0px 0px 4px white;
  outline: 0;
}

.overpanel .header h1 {
  font-size: 32px;
  font-weight: 300;
  padding: 0;
  margin: 0;
  color: white;
}

.overpanel .body {
   padding: 16px 16px 8px 16px;
}

.overpanel .footer {
  text-align: center;
  padding: 0px 16px 8px 16px;
}

.overpanel .footer hr {
  border: 0.5px solid #B9BCC0;
}
