.alert-banner {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: space-between;
  background: #F8F8F9;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.20);
  padding: 16px;
}

.alert-banner__details {
  display: flex;
  align-items: center;
}

.alert-banner__text-wrapper {
  margin: 0 1rem;
}

.alert-banner__title {
  margin: 0 0.25rem 0 0;
  line-height: 1.125;
  font-size: 24px;
  font-weight: 300;
  color: #515761;
}

.alert-banner__subtitle {
  font-size: 0.875rem;
  line-height: 1.125;
  margin: 0;
}

.alert-banner--icon {
  width: 32px;
  height: 32px;
}

