.inline-banner {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: space-between;
  background: #F8F8F9;
  padding: 16px;
}

.inline-banner__title {
  font-weight: 400;
  font-size: 16px;
  padding: 0;
  margin: 0;
}

.inline-banner--warning {
  background: #FFFAEF;
  border: 1px solid #926C20;

  .inline-banner__title {
    color: #515761;
  }
}

.inline-banner--error {
  background: #FDF0F0;
  border: 1px solid #E4292F;

  .inline-banner__title {
    color: #D8171B;
  }
}

.inline-banner--info {
  background: #EDF8FD;
  border: 1px solid #005EC3;

  .inline-banner__title {
    color: #515761;
  }
}

.inline-banner--success {
  background: #F1F6ED;
  border: 1px solid #3C8700;

  .inline-banner__title {
    color: #256700;
  }
}
